
body {
    display: grid;
    grid-template-rows: 1fr;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.header {
    display: flex;
    flex-wrap: wrap;
    background-color: #AF92C8;
    grid-column: 1 / 6;
}


.banner {
        display: flex;
        flex-wrap: wrap;
        background-color: #AF92C8;
        grid-column: 1 / 6;
        max-width: 100%;
        height: 300px;

}
#title {
    font-size: 10em;
    padding-left: 48px;
    margin-top: 72px;
}
.loginButton {
    border-radius:0.15em;
    border-color: gray;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    text-transform:uppercase;
    font-weight:400;
    color:black;
    background-color:#B2BD7E;
    box-shadow:inset 0 -0.6em 0 -0.35em rgba(0,0,0,0.17);
    text-align:center;
    width: 200px;
    height: 37px;
}
.signupButton {
    border-radius:0.15em;
    border-color: gray;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    text-transform:uppercase;
    font-weight:400;
    color:black;
    background-color:#B2BD7E;
    box-shadow:inset 0 -0.6em 0 -0.35em rgba(0,0,0,0.17);
    text-align:center;
    width: 200px;
}
.signinButton {
        padding: 10px;
        display: inline;
        background: none;
        border: none;
        margin-left: 55px;
        margin-bottom: 65px;
        border-radius:0.15em;
        border-color: gray;
        box-sizing: border-box;
        text-decoration:none;
        font-family:'Roboto',sans-serif;
        text-transform:uppercase;
        font-weight:400;
        color:black;
        background-color:#B2BD7E;
        box-shadow:inset 0 -0.6em 0 -0.35em rgba(0,0,0,0.17);
        text-align:center;
}
.verifyButton {
    /* top: 200px;
    right: 200px; */
    display:inline-block;
    padding:0.7em 1.4em;
    margin:0 0.3em 0.3em 0;
    border-radius:0.15em;
    border-color: gray;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    text-transform:uppercase;
    font-weight:400;
    color:black;
    background-color:#B2BD7E;
    box-shadow:inset 0 -0.6em 0 -0.35em rgba(0,0,0,0.17);
    text-align:center;  
}
.logoutButton{
    animation:bouncy 5s infinite linear;
    top: 200px;
    right: 200px;
    display:inline-block;
    padding:0.7em 1.4em;
    margin:0 0.3em 0.3em 0;
    border-radius:0.15em;
    border-color: gray;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    text-transform:uppercase;
    font-weight:400;
    color:black;
    background-color:#B2BD7E;
    box-shadow:inset 0 -0.6em 0 -0.35em rgba(0,0,0,0.17);
    text-align:center;
}
    .logoutButton:active{
        top:0.1em;
        top: 200px;
        right: 200px;
    }
    @media all and (max-width:30em){
        .logoutButton{
            display:block;
            margin:0.4em auto;
        }
    }

    .bouncy{
        animation:bouncy 5s infinite linear;
        position:relative;
        }
        @keyframes bouncy {
            0%{top:0em}
            40%{top:0em}
            43%{top:-0.9em}
            46%{top:0em}
            48%{top:-0.4em}
            50%{top:0em}
            100%{top:0em;}
        }
    
